import { useEffect, useState } from 'react';
import { useAuth } from '../../../contexts/AuthContext';
import { supabase } from '../../../lib/supabase';
import { useNavigate } from 'react-router-dom';

interface WorkTask {
  id: string;
  title: string;
  category: 'light_work' | 'craft' | 'pc' | 'it' | 'external';
  description?: string;
  start_date: string;
  end_date: string;
  created_at: string;
}

interface WorkProgress {
  id: string;
  task_id: string;
  user_id: string;
  date: string;
  progress_rate: number;
  notes?: string;
}

// 日本時間の日付を取得する関数
const getJSTDate = (date: Date = new Date()): string => {
  const jstDate = new Date(date.toLocaleString('en-US', { timeZone: 'Asia/Tokyo' }));
  const year = jstDate.getFullYear();
  const month = String(jstDate.getMonth() + 1).padStart(2, '0');
  const day = String(jstDate.getDate()).padStart(2, '0');
  return `${year}-${month}-${day}`;
};

export default function UserWork() {
  const { profile } = useAuth();
  const navigate = useNavigate();
  const [viewMode, setViewMode] = useState<'list' | 'gantt' | 'chart'>('list');
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [tasks, setTasks] = useState<WorkTask[]>([]);
  const [progressData, setProgressData] = useState<WorkProgress[]>([]);
  const [loading, setLoading] = useState(true);
  const [showProgressModal, setShowProgressModal] = useState(false);
  const [selectedTask, setSelectedTask] = useState<WorkTask | null>(null);
  const [progressInput, setProgressInput] = useState({
    progress_rate: 0,
    notes: '',
  });

  useEffect(() => {
    if (profile?.role !== 'user') {
      navigate('/admin/dashboard');
      return;
    }
    fetchTasks();
    fetchProgress();
  }, [profile, selectedDate]);

  const fetchTasks = async () => {
    setLoading(true);
    const { data } = await supabase
      .from('work_tasks')
      .select('*')
      .order('start_date', { ascending: false });

    if (data) {
      setTasks(data);
    }
    setLoading(false);
  };

  const fetchProgress = async () => {
    if (!profile?.id) return;

    const { data } = await supabase
      .from('work_progress')
      .select('*')
      .eq('user_id', profile.id);

    if (data) {
      setProgressData(data);
    }
  };

  const handleAddProgress = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!profile?.id || !selectedTask) return;

    const dateStr = selectedDate.toISOString().split('T')[0];

    // 既存の記録をチェック
    const existingProgress = progressData.find(
      p => p.task_id === selectedTask.id && p.date === dateStr
    );

    if (existingProgress) {
      // 更新
      const { error } = await supabase
        .from('work_progress')
        .update({
          progress_rate: progressInput.progress_rate,
          notes: progressInput.notes,
        })
        .eq('id', existingProgress.id);

      if (!error) {
        setShowProgressModal(false);
        setProgressInput({ progress_rate: 0, notes: '' });
        setSelectedTask(null);
        fetchProgress();
      }
    } else {
      // 新規追加
      const { error } = await supabase.from('work_progress').insert([
        {
          task_id: selectedTask.id,
          user_id: profile.id,
          date: dateStr,
          progress_rate: progressInput.progress_rate,
          notes: progressInput.notes,
        },
      ]);

      if (!error) {
        setShowProgressModal(false);
        setProgressInput({ progress_rate: 0, notes: '' });
        setSelectedTask(null);
        fetchProgress();
      }
    }
  };

  const openProgressModal = (task: WorkTask) => {
    setSelectedTask(task);
    const dateStr = selectedDate.toISOString().split('T')[0];
    const existingProgress = progressData.find(
      p => p.task_id === task.id && p.date === dateStr
    );

    if (existingProgress) {
      setProgressInput({
        progress_rate: existingProgress.progress_rate,
        notes: existingProgress.notes || '',
      });
    } else {
      setProgressInput({ progress_rate: 0, notes: '' });
    }
    setShowProgressModal(true);
  };

  const getCategoryLabel = (category: string) => {
    const labels: Record<string, string> = {
      light_work: '軽作業',
      craft: 'クラフト',
      pc: 'PC',
      it: 'IT',
      external: '外部',
    };
    return labels[category] || category;
  };

  const getCategoryColor = (category: string) => {
    const colors: Record<string, string> = {
      light_work: 'bg-blue-500',
      craft: 'bg-green-500',
      pc: 'bg-purple-500',
      it: 'bg-orange-500',
      external: 'bg-pink-500',
    };
    return colors[category] || 'bg-gray-500';
  };

  const getProgressForTask = (taskId: string) => {
    const progress = progressData.filter(p => p.task_id === taskId);
    if (progress.length === 0) return 0;
    const total = progress.reduce((sum, p) => sum + p.progress_rate, 0);
    return Math.round(total / progress.length);
  };

  const getTodayProgress = (taskId: string) => {
    const dateStr = selectedDate.toISOString().split('T')[0];
    const progress = progressData.find(p => p.task_id === taskId && p.date === dateStr);
    return progress?.progress_rate || 0;
  };

  const previousDay = () => {
    const newDate = new Date(selectedDate);
    newDate.setDate(newDate.getDate() - 1);
    setSelectedDate(newDate);
  };

  const nextDay = () => {
    const newDate = new Date(selectedDate);
    newDate.setDate(newDate.getDate() + 1);
    setSelectedDate(newDate);
  };

  const getTasksForDate = () => {
    const dateStr = selectedDate.toISOString().split('T')[0];
    return tasks.filter(task => {
      return task.start_date <= dateStr && task.end_date >= dateStr;
    });
  };

  const getGanttData = () => {
    const today = new Date();
    const startOfMonth = new Date(today.getFullYear(), today.getMonth(), 1);
    const endOfMonth = new Date(today.getFullYear(), today.getMonth() + 1, 0);
    const daysInMonth = endOfMonth.getDate();

    return { startOfMonth, endOfMonth, daysInMonth };
  };

  const isTaskActiveOnDay = (task: WorkTask, day: number) => {
    const { startOfMonth } = getGanttData();
    const checkDate = new Date(startOfMonth.getFullYear(), startOfMonth.getMonth(), day);
    const checkDateStr = checkDate.toISOString().split('T')[0];
    return task.start_date <= checkDateStr && task.end_date >= checkDateStr;
  };

  // グラフデータ取得関数
  const getLast7DaysProgress = () => {
    const days = [];
    for (let i = 6; i >= 0; i--) {
      const date = new Date();
      date.setDate(date.getDate() - i);
      const dateStr = date.toISOString().split('T')[0];
      const dayProgress = progressData.filter(p => p.date === dateStr);
      const avgProgress = dayProgress.length > 0
        ? Math.round(dayProgress.reduce((sum, p) => sum + p.progress_rate, 0) / dayProgress.length)
        : 0;
      days.push({
        date: dateStr,
        label: `${date.getMonth() + 1}/${date.getDate()}`,
        progress: avgProgress,
      });
    }
    return days;
  };

  const getCategoryDistribution = () => {
    const distribution: Record<string, number> = {
      light_work: 0,
      craft: 0,
      pc: 0,
      it: 0,
      external: 0,
    };

    tasks.forEach(task => {
      const taskProgress = progressData.filter(p => p.task_id === task.id);
      if (taskProgress.length > 0) {
        distribution[task.category]++;
      }
    });

    return Object.entries(distribution)
      .filter(([_, count]) => count > 0)
      .map(([category, count]) => ({
        category,
        label: getCategoryLabel(category),
        count,
        color: getCategoryColor(category),
      }));
  };

  const getTaskProgressData = () => {
    return tasks.slice(0, 5).map(task => ({
      task,
      progress: getProgressForTask(task.id),
    }));
  };

  const dailyTasks = getTasksForDate();
  const { daysInMonth } = getGanttData();
  const last7Days = getLast7DaysProgress();
  const categoryDist = getCategoryDistribution();
  const taskProgressData = getTaskProgressData();
  const totalCategories = categoryDist.reduce((sum, item) => sum + item.count, 0);

  return (
    <div className="min-h-screen bg-gray-50">
      <header className="bg-white shadow-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <button
                onClick={() => navigate('/user/dashboard')}
                className="w-10 h-10 flex items-center justify-center rounded-lg hover:bg-gray-100 transition-colors cursor-pointer"
              >
                <i className="ri-arrow-left-line text-xl text-gray-600"></i>
              </button>
              <div>
                <h1 className="text-2xl font-bold text-gray-900">作業記録</h1>
                <p className="text-sm text-gray-600 mt-1">作業内容と進捗を確認</p>
              </div>
            </div>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="mb-6">
          <div className="flex items-center gap-2 bg-white rounded-lg p-1 shadow-sm border border-gray-200 inline-flex">
            <button
              onClick={() => setViewMode('list')}
              className={`px-4 py-2 rounded-md text-sm font-medium transition-colors cursor-pointer whitespace-nowrap ${
                viewMode === 'list'
                  ? 'bg-blue-500 text-white'
                  : 'text-gray-600 hover:bg-gray-100'
              }`}
            >
              <i className="ri-list-check mr-2"></i>
              日別一覧
            </button>
            <button
              onClick={() => setViewMode('gantt')}
              className={`px-4 py-2 rounded-md text-sm font-medium transition-colors cursor-pointer whitespace-nowrap ${
                viewMode === 'gantt'
                  ? 'bg-blue-500 text-white'
                  : 'text-gray-600 hover:bg-gray-100'
              }`}
            >
              <i className="ri-bar-chart-horizontal-line mr-2"></i>
              ガントチャート
            </button>
            <button
              onClick={() => setViewMode('chart')}
              className={`px-4 py-2 rounded-md text-sm font-medium transition-colors cursor-pointer whitespace-nowrap ${
                viewMode === 'chart'
                  ? 'bg-blue-500 text-white'
                  : 'text-gray-600 hover:bg-gray-100'
              }`}
            >
              <i className="ri-pie-chart-line mr-2"></i>
              グラフ分析
            </button>
          </div>
        </div>

        {viewMode === 'list' ? (
          <>
            <div className="mb-6">
              <div className="bg-gradient-to-r from-purple-500 to-indigo-600 rounded-xl p-6 text-white">
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-4">
                    <button
                      onClick={previousDay}
                      className="w-10 h-10 flex items-center justify-center rounded-lg hover:bg-white/20 transition-colors cursor-pointer"
                    >
                      <i className="ri-arrow-left-s-line text-xl"></i>
                    </button>
                    <div>
                      <p className="text-sm opacity-90 mb-1">選択日</p>
                      <p className="text-2xl font-bold">
                        {selectedDate.toLocaleDateString('ja-JP', {
                          year: 'numeric',
                          month: 'long',
                          day: 'numeric',
                          weekday: 'long',
                        })}
                      </p>
                    </div>
                    <button
                      onClick={nextDay}
                      className="w-10 h-10 flex items-center justify-center rounded-lg hover:bg-white/20 transition-colors cursor-pointer"
                    >
                      <i className="ri-arrow-right-s-line text-xl"></i>
                    </button>
                  </div>
                  <div className="w-16 h-16 bg-white/20 rounded-full flex items-center justify-center">
                    <i className="ri-task-line text-3xl"></i>
                  </div>
                </div>
              </div>
            </div>

            <div className="bg-white rounded-xl shadow-sm border border-gray-100">
              <div className="p-4 border-b border-gray-100">
                <h3 className="text-lg font-bold text-gray-900">本日の作業一覧</h3>
              </div>
              {loading ? (
                <div className="p-8 text-center text-gray-500">読み込み中...</div>
              ) : dailyTasks.length === 0 ? (
                <div className="p-8 text-center text-gray-500">
                  <i className="ri-inbox-line text-4xl mb-2"></i>
                  <p>本日の作業はありません</p>
                </div>
              ) : (
                <div className="divide-y divide-gray-100">
                  {dailyTasks.map((task) => {
                    const progress = getProgressForTask(task.id);
                    const todayProgress = getTodayProgress(task.id);
                    return (
                      <div key={task.id} className="p-6 hover:bg-gray-50 transition-colors">
                        <div className="flex items-start justify-between mb-4">
                          <div className="flex items-start gap-4 flex-1">
                            <div className={`${getCategoryColor(task.category)} w-12 h-12 rounded-lg flex items-center justify-center text-white flex-shrink-0`}>
                              <i className="ri-briefcase-line text-xl"></i>
                            </div>
                            <div className="flex-1">
                              <div className="flex items-center gap-2 mb-2">
                                <span className={`${getCategoryColor(task.category)} text-white text-xs font-medium px-3 py-1 rounded-full`}>
                                  {getCategoryLabel(task.category)}
                                </span>
                              </div>
                              <h4 className="font-bold text-gray-900 mb-2">{task.title}</h4>
                              {task.description && (
                                <p className="text-sm text-gray-600 mb-3">{task.description}</p>
                              )}
                              <div className="flex items-center gap-4 text-sm text-gray-500">
                                <div className="flex items-center gap-1">
                                  <i className="ri-calendar-line"></i>
                                  <span>
                                    {new Date(task.start_date).toLocaleDateString('ja-JP')} 〜 {new Date(task.end_date).toLocaleDateString('ja-JP')}
                                  </span>
                                </div>
                              </div>
                            </div>
                          </div>
                          <button
                            onClick={() => openProgressModal(task)}
                            className="px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors text-sm font-medium whitespace-nowrap cursor-pointer"
                          >
                            <i className="ri-edit-line mr-2"></i>
                            記録入力
                          </button>
                        </div>
                        <div className="space-y-3">
                          <div>
                            <div className="flex items-center justify-between mb-2">
                              <span className="text-sm font-medium text-gray-700">本日の進捗</span>
                              <span className="text-sm font-bold text-gray-900">{todayProgress}%</span>
                            </div>
                            <div className="w-full bg-gray-200 rounded-full h-3">
                              <div
                                className={`${getCategoryColor(task.category)} h-3 rounded-full transition-all`}
                                style={{ width: `${todayProgress}%` }}
                              ></div>
                            </div>
                          </div>
                          <div>
                            <div className="flex items-center justify-between mb-2">
                              <span className="text-sm font-medium text-gray-700">全体進捗</span>
                              <span className="text-sm font-bold text-gray-900">{progress}%</span>
                            </div>
                            <div className="w-full bg-gray-200 rounded-full h-2">
                              <div
                                className={`${getCategoryColor(task.category)} h-2 rounded-full transition-all`}
                                style={{ width: `${progress}%` }}
                              ></div>
                            </div>
                          </div>
                        </div>
                      </div>
                    );
                  })}
                </div>
              )}
            </div>
          </>
        ) : viewMode === 'gantt' ? (
          <>
            <div className="mb-6">
              <div className="flex items-center justify-between">
                <h3 className="text-lg font-bold text-gray-900">月間ガントチャート</h3>
                <span className="text-sm font-medium text-gray-900">
                  {new Date().getFullYear()}年{new Date().getMonth() + 1}月
                </span>
              </div>
            </div>

            <div className="bg-white rounded-xl shadow-sm border border-gray-100 overflow-x-auto">
              <table className="w-full">
                <thead>
                  <tr className="border-b border-gray-200">
                    <th className="p-4 text-left text-sm font-bold text-gray-900 sticky left-0 bg-white z-10 min-w-[200px]">
                      作業名
                    </th>
                    <th className="p-4 text-left text-sm font-bold text-gray-900 min-w-[100px]">
                      カテゴリ
                    </th>
                    <th className="p-4 text-left text-sm font-bold text-gray-900 min-w-[100px]">
                      進捗
                    </th>
                    {Array.from({ length: daysInMonth }, (_, i) => i + 1).map((day) => (
                      <th key={day} className="p-2 text-center text-xs font-medium text-gray-600 min-w-[40px]">
                        {day}
                      </th>
                    ))}
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-100">
                  {tasks.map((task) => {
                    const progress = getProgressForTask(task.id);
                    return (
                      <tr key={task.id} className="hover:bg-gray-50">
                        <td className="p-4 sticky left-0 bg-white z-10">
                          <p className="font-medium text-gray-900 text-sm">{task.title}</p>
                        </td>
                        <td className="p-4">
                          <span className={`${getCategoryColor(task.category)} text-white text-xs font-medium px-3 py-1 rounded-full whitespace-nowrap`}>
                            {getCategoryLabel(task.category)}
                          </span>
                        </td>
                        <td className="p-4">
                          <div className="flex items-center gap-2">
                            <div className="w-16 bg-gray-200 rounded-full h-2">
                              <div
                                className={`${getCategoryColor(task.category)} h-2 rounded-full`}
                                style={{ width: `${progress}%` }}
                              ></div>
                            </div>
                            <span className="text-xs font-medium text-gray-700 whitespace-nowrap">{progress}%</span>
                          </div>
                        </td>
                        {Array.from({ length: daysInMonth }, (_, i) => i + 1).map((day) => {
                          const isActive = isTaskActiveOnDay(task, day);
                          return (
                            <td key={day} className="p-2">
                              {isActive && (
                                <div className={`${getCategoryColor(task.category)} h-8 rounded`}></div>
                              )}
                            </td>
                          );
                        })}
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>

            <div className="mt-6 bg-white rounded-xl p-6 shadow-sm border border-gray-100">
              <h4 className="text-sm font-bold text-gray-900 mb-4">カテゴリ凡例</h4>
              <div className="grid grid-cols-2 md:grid-cols-5 gap-4">
                <div className="flex items-center gap-2">
                  <div className="w-8 h-8 bg-blue-500 rounded-lg"></div>
                  <span className="text-sm text-gray-600">軽作業</span>
                </div>
                <div className="flex items-center gap-2">
                  <div className="w-8 h-8 bg-green-500 rounded-lg"></div>
                  <span className="text-sm text-gray-600">クラフト</span>
                </div>
                <div className="flex items-center gap-2">
                  <div className="w-8 h-8 bg-purple-500 rounded-lg"></div>
                  <span className="text-sm text-gray-600">PC</span>
                </div>
                <div className="flex items-center gap-2">
                  <div className="w-8 h-8 bg-orange-500 rounded-lg"></div>
                  <span className="text-sm text-gray-600">IT</span>
                </div>
                <div className="flex items-center gap-2">
                  <div className="w-8 h-8 bg-pink-500 rounded-lg"></div>
                  <span className="text-sm text-gray-600">外部</span>
                </div>
              </div>
            </div>
          </>
        ) : (
          <>
            <div className="mb-6">
              <h3 className="text-lg font-bold text-gray-900">進捗グラフ分析</h3>
              <p className="text-sm text-gray-600 mt-1">あなたの作業進捗を可視化</p>
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-6">
              {/* 日別進捗推移グラフ */}
              <div className="bg-white rounded-xl shadow-sm border border-gray-100 p-6">
                <div className="flex items-center justify-between mb-6">
                  <h4 className="text-base font-bold text-gray-900">過去7日間の進捗推移</h4>
                  <i className="ri-line-chart-line text-2xl text-blue-500"></i>
                </div>
                <div className="space-y-4">
                  {last7Days.map((day, index) => (
                    <div key={index}>
                      <div className="flex items-center justify-between mb-2">
                        <span className="text-sm font-medium text-gray-700">{day.label}</span>
                        <span className="text-sm font-bold text-blue-500">{day.progress}%</span>
                      </div>
                      <div className="relative h-8 bg-gray-100 rounded-lg overflow-hidden">
                        <div
                          className="absolute top-0 left-0 h-full bg-gradient-to-r from-blue-400 to-blue-600 rounded-lg transition-all duration-500"
                          style={{ width: `${day.progress}%` }}
                        ></div>
                        <div className="absolute inset-0 flex items-center justify-center">
                          <span className="text-xs font-medium text-gray-600">{day.progress}%</span>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>

              {/* カテゴリ別作業分布 */}
              <div className="bg-white rounded-xl shadow-sm border border-gray-100 p-6">
                <div className="flex items-center justify-between mb-6">
                  <h4 className="text-base font-bold text-gray-900">カテゴリ別作業分布</h4>
                  <i className="ri-pie-chart-line text-2xl text-purple-500"></i>
                </div>
                {categoryDist.length === 0 ? (
                  <div className="text-center py-8 text-gray-500">
                    <i className="ri-inbox-line text-4xl mb-2"></i>
                    <p className="text-sm">作業データがありません</p>
                  </div>
                ) : (
                  <div className="space-y-4">
                    <div className="flex items-center justify-center mb-6">
                      <div className="relative w-48 h-48">
                        <svg viewBox="0 0 100 100" className="transform -rotate-90">
                          {categoryDist.map((item, index) => {
                            const percentage = (item.count / totalCategories) * 100;
                            const prevPercentages = categoryDist
                              .slice(0, index)
                              .reduce((sum, i) => sum + (i.count / totalCategories) * 100, 0);
                            const strokeDasharray = `${percentage} ${100 - percentage}`;
                            const strokeDashoffset = -prevPercentages;
                            const colorMap: Record<string, string> = {
                              'bg-blue-500': '#3b82f6',
                              'bg-green-500': '#22c55e',
                              'bg-purple-500': '#a855f7',
                              'bg-orange-500': '#f97316',
                              'bg-pink-500': '#ec4899',
                            };
                            return (
                              <circle
                                key={index}
                                cx="50"
                                cy="50"
                                r="15.915"
                                fill="transparent"
                                stroke={colorMap[item.color] || '#6b7280'}
                                strokeWidth="31.83"
                                strokeDasharray={strokeDasharray}
                                strokeDashoffset={strokeDashoffset}
                              />
                            );
                          })}
                        </svg>
                        <div className="absolute inset-0 flex items-center justify-center">
                          <div className="text-center">
                            <p className="text-2xl font-bold text-gray-900">{totalCategories}</p>
                            <p className="text-xs text-gray-600">作業</p>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="space-y-3">
                      {categoryDist.map((item, index) => {
                        const percentage = Math.round((item.count / totalCategories) * 100);
                        return (
                          <div key={index} className="flex items-center justify-between">
                            <div className="flex items-center gap-3">
                              <div className={`w-4 h-4 ${item.color} rounded`}></div>
                              <span className="text-sm font-medium text-gray-700">{item.label}</span>
                            </div>
                            <div className="flex items-center gap-2">
                              <span className="text-sm text-gray-600">{item.count}件</span>
                              <span className="text-sm font-bold text-gray-900">{percentage}%</span>
                            </div>
                          </div>
                        );
                      })}
                    </div>
                  </div>
                )}
              </div>
            </div>

            {/* 作業別進捗状況 */}
            <div className="bg-white rounded-xl shadow-sm border border-gray-100 p-6">
              <div className="flex items-center justify-between mb-6">
                <h4 className="text-base font-bold text-gray-900">作業別進捗状況（上位5件）</h4>
                <i className="ri-bar-chart-box-line text-2xl text-green-500"></i>
              </div>
              {taskProgressData.length === 0 ? (
                <div className="text-center py-8 text-gray-500">
                  <i className="ri-inbox-line text-4xl mb-2"></i>
                  <p className="text-sm">作業データがありません</p>
                </div>
              ) : (
                <div className="space-y-6">
                  {taskProgressData.map((item, index) => (
                    <div key={index}>
                      <div className="flex items-center justify-between mb-3">
                        <div className="flex items-center gap-3 flex-1">
                          <span className={`${getCategoryColor(item.task.category)} text-white text-xs font-medium px-3 py-1 rounded-full whitespace-nowrap`}>
                            {getCategoryLabel(item.task.category)}
                          </span>
                          <span className="text-sm font-medium text-gray-900 truncate">{item.task.title}</span>
                        </div>
                        <span className="text-sm font-bold text-gray-900 ml-4">{item.progress}%</span>
                      </div>
                      <div className="relative h-6 bg-gray-100 rounded-lg overflow-hidden">
                        <div
                          className={`absolute top-0 left-0 h-full ${getCategoryColor(item.task.category)} rounded-lg transition-all duration-500`}
                          style={{ width: `${item.progress}%` }}
                        ></div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>

            {/* 統計サマリー */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mt-6">
              <div className="bg-gradient-to-br from-blue-500 to-blue-600 rounded-xl p-6 text-white">
                <div className="flex items-center justify-between mb-4">
                  <div className="w-12 h-12 bg-white/20 rounded-lg flex items-center justify-center">
                    <i className="ri-task-line text-2xl"></i>
                  </div>
                  <i className="ri-arrow-up-line text-xl"></i>
                </div>
                <p className="text-sm opacity-90 mb-1">総作業数</p>
                <p className="text-3xl font-bold">{tasks.length}</p>
              </div>

              <div className="bg-gradient-to-br from-green-500 to-green-600 rounded-xl p-6 text-white">
                <div className="flex items-center justify-between mb-4">
                  <div className="w-12 h-12 bg-white/20 rounded-lg flex items-center justify-center">
                    <i className="ri-check-line text-2xl"></i>
                  </div>
                  <i className="ri-arrow-up-line text-xl"></i>
                </div>
                <p className="text-sm opacity-90 mb-1">進捗記録数</p>
                <p className="text-3xl font-bold">{progressData.length}</p>
              </div>

              <div className="bg-gradient-to-br from-purple-500 to-purple-600 rounded-xl p-6 text-white">
                <div className="flex items-center justify-between mb-4">
                  <div className="w-12 h-12 bg-white/20 rounded-lg flex items-center justify-center">
                    <i className="ri-percent-line text-2xl"></i>
                  </div>
                  <i className="ri-arrow-up-line text-xl"></i>
                </div>
                <p className="text-sm opacity-90 mb-1">平均進捗率</p>
                <p className="text-3xl font-bold">
                  {tasks.length > 0
                    ? Math.round(tasks.reduce((sum, task) => sum + getProgressForTask(task.id), 0) / tasks.length)
                    : 0}%
                </p>
              </div>
            </div>
          </>
        )}
      </main>

      {showProgressModal && selectedTask && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-xl max-w-2xl w-full max-h-[90vh] overflow-y-auto">
            <div className="p-6 border-b border-gray-100">
              <div className="flex items-center justify-between">
                <h3 className="text-xl font-bold text-gray-900">作業記録入力</h3>
                <button
                  onClick={() => {
                    setShowProgressModal(false);
                    setSelectedTask(null);
                    setProgressInput({ progress_rate: 0, notes: '' });
                  }}
                  className="w-8 h-8 flex items-center justify-center rounded-lg hover:bg-gray-100 transition-colors cursor-pointer"
                >
                  <i className="ri-close-line text-xl text-gray-600"></i>
                </button>
              </div>
            </div>
            <form onSubmit={handleAddProgress} className="p-6 space-y-4">
              <div className="bg-gray-50 rounded-lg p-4">
                <div className="flex items-center gap-3 mb-2">
                  <span className={`${getCategoryColor(selectedTask.category)} text-white text-xs font-medium px-3 py-1 rounded-full`}>
                    {getCategoryLabel(selectedTask.category)}
                  </span>
                </div>
                <h4 className="font-bold text-gray-900 mb-1">{selectedTask.title}</h4>
                {selectedTask.description && (
                  <p className="text-sm text-gray-600">{selectedTask.description}</p>
                )}
                <p className="text-sm text-gray-500 mt-2">
                  記録日：{selectedDate.toLocaleDateString('ja-JP', {
                    year: 'numeric',
                    month: 'long',
                    day: 'numeric',
                    weekday: 'long',
                  })}
                </p>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  進捗率 <span className="text-red-500">*</span>
                </label>
                <div className="space-y-3">
                  <input
                    type="range"
                    min="0"
                    max="100"
                    step="5"
                    value={progressInput.progress_rate}
                    onChange={(e) => setProgressInput({ ...progressInput, progress_rate: parseInt(e.target.value) })}
                    className="w-full h-2 bg-gray-200 rounded-lg appearance-none cursor-pointer"
                  />
                  <div className="flex items-center justify-between">
                    <span className="text-sm text-gray-600">0%</span>
                    <span className="text-2xl font-bold text-blue-500">{progressInput.progress_rate}%</span>
                    <span className="text-sm text-gray-600">100%</span>
                  </div>
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  作業メモ
                </label>
                <textarea
                  value={progressInput.notes}
                  onChange={(e) => setProgressInput({ ...progressInput, notes: e.target.value })}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  rows={4}
                  placeholder="本日の作業内容や気づいたことを記入してください"
                />
              </div>

              <div className="flex gap-3 pt-4">
                <button
                  type="button"
                  onClick={() => {
                    setShowProgressModal(false);
                    setSelectedTask(null);
                    setProgressInput({ progress_rate: 0, notes: '' });
                  }}
                  className="flex-1 px-4 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 transition-colors font-medium whitespace-nowrap cursor-pointer"
                >
                  キャンセル
                </button>
                <button
                  type="submit"
                  className="flex-1 px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors font-medium whitespace-nowrap cursor-pointer"
                >
                  保存
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}
